* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

@font-face {
  font-family: 'CircularSpotifyBold';
  src: url('./static/fonts/CircularSpotifyText-Bold.otf') format('opentype');
  font-weight: normal;
  font-style: normal;
}

@font-face {
  font-family: 'CircularSpotifyBook';
  src: url('./static/fonts/CircularSpotifyText-Book.otf') format('opentype');
  font-weight: normal;
  font-style: normal;
}

@font-face {
  font-family: 'CircularSpotifyLight';
  src: url('./static/fonts/CircularSpotifyText-Light.otf') format ('opentype');
  font-weight: normal;
  font-style: normal;
}

/* Style the scrollbar itself (background) */
::-webkit-scrollbar {
  width: 8px; /* for vertical scrollbars */
  height: 8px; /* for horizontal scrollbars */
}

::-webkit-scrollbar-thumb {
  background: #656565; /* thumb color */
  border-radius: 6px;
}

html, body {
  height: 100%;
  width: 100%;
  margin: 0;
  padding: 0;
  font-size: 16px;
}

body {
  -webkit-user-select: none; /* Safari */
  -moz-user-select: none; /* Firefox */
  -ms-user-select: none; /* Internet Explorer/Edge */
  user-select: none;
}

h1, h2, h3, h4, h5, label, input, span {
  font-family: 'CircularSpotifyBold', 'Helvetica Neue', 'Helvetica', 'Arial', sans-serif;
  color: white;
}

li {
  font-family: 'CircularSpotifyBook', 'Helvetica Neue', 'Helvetica', 'Arial', sans-serif;
  font-weight: normal;
  font-style: normal;
}


p {
  font-family: 'CircularSpotifyBook', 'Helvetica Neue', 'Helvetica', 'Arial', sans-serif;
}

h1 {
  font-size: 3rem;
}

label {
  font-size: 1rem;
}

body {
  font-size: 1rem;
  color: white;
  font-family: 'CircularSpotifyLight', 'Helvetica Neue', 'Helvetica', 'Arial', sans-serif;
}

input::placeholder {
  color: gray;
  font-family: 'CircularSpotifyBook', 'Helvetica Neue', 'Helvetica', 'Arial', sans-serif;
  font-size: 1rem;
  padding-top: 0.5rem;
}

.background {
  margin: 0;
  padding: 0;
  width: 100vw;
  height: 100vh;
  background-color: #000000;
}

.standard-div-background {
  background-color: #121212;
  border-radius: 0.5rem;
}

.helper-text {
  color: grey;
  font-size: 0.875rem;
}

/* * Form Container */
.form-container form {
  display: flex;
  flex-direction: column;
}


.text-input {
  /* width: 22rem; */
  height: 50px;
  border-radius: 0.35rem;
  margin-top: 0.55rem;
  margin-bottom: 0rem;
  background-color: #121212;
  border-color: #727272;
  border-width: 1px;
  border-style: solid;
  color: white;
  font-size: 1rem;
  font-family: 'CircularSpotifyBook', 'Helvetica Neue', 'Helvetica', 'Arial', sans-serif;
  padding-inline: 0.875rem;
}

.form-container button {
  font-family: 'CircularSpotifyBold';
  font-size: 1rem;
  background-color: #1DB954;
  /* max-width: 22rem; */
  width: 22rem;
  border-radius: 30px;
  margin-top: 3rem;
  border-style: none;
  height: 50px;
}


